$main-blue: #85bef5

.sidebar
    display: flex
    flex-wrap: wrap
    width: 18vw
    // margin: 20px
    background-color: $main-blue
    position: absolute

    .pop-up-window

        // display: flex
        flex-wrap: wrap
        // width: 18%
        // margin: 20px

        .pop-up-window__topmenu
            background-color: $main-blue
            display: block
            text-align: center
            text-transform: uppercase
            margin: 0.7vw
            font-size: 1.20vw
            line-height: 2vw
            cursor: pointer
            font-weight: 600
            color: #333333b7
            padding: 0.2vw
            transition-duration: .3s
            li
                list-style: none
                background-color: $main-blue
                cursor: pointer
                // margin-top: 25px
                // margin-bottom: 0
                // margin-bottom: 25px
                padding: 1vh
                a
                    background-color: $main-blue
                ul
                    background-color: $main-blue
                    transition-duration: .3s
                    left: 16vw
                    color: green
                    opacity: 0
                    margin: 2.1vw
                    margin-top: -2.5vw
                    // margin-left: 15px
                    width: 18vw
                    visibility: hidden
                    color: black
                    position: absolute
                    // margin: 50px

            li + li
                border-top: 1px solid #3335
            li:hover
                color: blue
                transition-duration: .1s
                z-index: 0
                // &:first-child > ul
                ul
                    opacity: 100%
                    visibility: visible
                    left: 16.5vw
                a
                    color: #1d51e0
                ul>li:first-child
                li:first-child
