.header
    display: grid
    grid-template-columns: 3fr 3fr 10fr 4fr
    // grid-template-rows: 1fr

    grid-gap: 2vw
    margin-top: 1vw
    margin-left: 1vw
    margin-right: 1vw
    position: relative
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
    margin-bottom: 0px

    // border: 1px solid black
    .header__logo
        margin-left: 5vw
        // display: flex
        // position: relative
        // border: 1px solid black
        // margin: auto

        img
            z-index: 0
            cursor: pointer
            margin-top: -25px
            // position: absolute
            height: 7em
            // border: 3px solid
    .header__info
        font-size: 1.3em
        .header__info-blure
            color: #0000006b
    .header__contacts
        display: flex
        // border: 1px solid black
        text-align: center
        // line-height: 165%
        line-height: 5vw
        .header__contact-info
            // border: 1px solid black
            color: #333333a4
            margin-left: 10px

        .header__contacts-phone
            // border: 1px solid black

            margin-left: 10px
            font-size: 1.4vw

    .header__social-networks
        // border: 1px solid black
        margin: 5px

        img
            cursor: pointer
            width: 2.2vw
            margin: 10px
            margin-left: 15px
            // border: 1px solid black
