.navigation
    // background-color: #837d7d13
    background-color: #85bef5
    box-shadow: 3px 3px 3px 3px #837d7d13
    height: 2.5vw
    padding: 0.8vw
    z-index: 10
    font-size: 1vw
    // border: 3px solid black

    .navigation__container

        display: grid
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr
        margin-left: 3vw
        margin-right: 3vw
        // border: 3px solid
        background-color: #85bef5
        margin-top: 0.2vw
        .navLink
            cursor: pointer
            font-family: Geneva, Verdana, sans-serif
            font-size: 0.9em
            text-transform: uppercase
            color: #2423239c
            text-decoration: none
            background-color: #85bef5
            margin-left: 5px
            text-align: center
            font-weight: 600
            margin-top: auto
            margin-bottom: auto
            // border: 3px solid
            // margin-top: 1.3vw
            line-height: 25px
            height: 1vw

            position: relative
            .navigation__strip
                height: 8px
                position: absolute
                background-color: red
                z-index: 100

        .navLink + .navLink
            // border-right: 1px solid

            border-left: 1px solid
